html {
  scroll-snap-type: y mandatory;
}
.app {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: black;

}

.app_top > h1 {
  text-align: center;
  color: white;
}

.app_top {
  margin-bottom: -90px;
}

.app_logo {
  height: 9vh;
}

.app_videos {
  position: relative;
  height: 73vh;
  background-color: white;
  width: 45%;
  border-radius: 20px;
  max-width: 450px;
  max-height: 1300px;
  overflow: scroll;
  scroll-snap-type: y mandatory;
}

.app_videos::-webkit-scrollbar {
  display: none;
}

.app_videos {
  -ms-overflow-style: none;
  scrollbar-width: none;
}




